import React, { Component } from 'react';
import linkedInIcon from './res/linkedin.svg';
import gitHubIcon from './res/github.svg';
import mailIcon from './res/mail.svg';
import resumeIcon from './res/resume.svg';
import resume from './res/resume.pdf';
import Favicon from 'react-favicon';
import favIcon from './res/favicon.ico';
import ProjectsView from './Projects.js';
import AboutView from './About.js';
import { Tabs } from './Constants.js';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = { view: ProjectsView, active_tab: Tabs.PORTFOLIO };
    document.title = "Portfolio | Mattias Johansson";
  }

  changeView(view, active_tab) {
    this.setState({
      view: view,
      active_tab
    })
  }

  getTabStyle(tab) {
    if (tab === this.state.active_tab) {
      return activeTabStyle
    }
  }

  render() {
    return (
      <React.Fragment>
        <Favicon url={favIcon} />
        <div className="header">
          <div className="header-wrapper">
            <div className="info-container">
              <div className="name-box">
                <h1>Mattias Johansson</h1>
                <div className="border-box"></div>
              </div>
              <div className="contact">
                <div className="contact-item">
                  <a target="_blank" href={resume}><img src={resumeIcon} /></a>
                </div>
                <div className="contact-item">
                  <a target="_blank" href="http://linkedin.com/in/mattias-johansson-19b770108"><img src={linkedInIcon} /></a>
                </div>
                <div className="contact-item">
                  <a target="_blank" href="http://github.com/MattiasJo"><img src={gitHubIcon} /></a>
                </div>
                <div className="contact-item">
                  <a target="_blank" href="mailto:johmatti@student.chalmers.se"><img src={mailIcon} /></a>
                </div>
              </div>
            </div>
            <div className="navigation">
              <a onClick={() => this.changeView(AboutView, Tabs.ABOUT)} style={this.getTabStyle(Tabs.ABOUT)}>About me</a>
              <a onClick={() => this.changeView(ProjectsView, Tabs.PORTFOLIO)} style={this.getTabStyle(Tabs.PORTFOLIO)}>Portfolio</a>
            </div>
          </div>
        </div>
        <this.state.view />
      </React.Fragment>
    );
  }
}

const activeTabStyle = {
  borderBottom: '0.2em solid #9b59b6'
}

export default App;
